'use client';

import { useEffect, useLayoutEffect, useRef } from 'react';
import { usePathname, useSearchParams } from 'next/navigation';

/**
 * Ï†ÑÏó≠ Ïä§ÌÅ¨Î°§ Í¥ÄÎ¶¨ Ïª¥Ìè¨ÎÑåÌä∏
 * - Ïã§ÏãúÍ∞Ñ Ïä§ÌÅ¨Î°§ ÏúÑÏπò Ï†ÄÏû•
 * - ÌéòÏù¥ÏßÄ ÏßÑÏûÖ/ÏÉàÎ°úÍ≥†Ïπ® Ïãú Ïä§ÌÅ¨Î°§ Î≥µÏõê
 * - Ïù¥ÎØ∏ÏßÄ Î°úÎî©ÏúºÎ°ú Ïù∏Ìïú Layout Shift ÎåÄÏùë (ResizeObserver)
 */
const ScrollManager = () => {
  const pathname = usePathname();
  const searchParams = useSearchParams();
  const fullPath = pathname + searchParams.toString();
  
  // Ïä§ÌÅ¨Î°§ Ï†ÄÏû• (Throttling Ï†ÅÏö©ÏúºÎ°ú ÏÑ±Îä• ÏµúÏ†ÅÌôî)
  useEffect(() => {
    let ticking = false;

    const handleScroll = () => {
      if (!ticking) {
        window.requestAnimationFrame(() => {
          sessionStorage.setItem(`scroll_pos:${fullPath}`, window.scrollY.toString());
          ticking = false;
        });
        ticking = true;
      }
    };

    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, [fullPath]);

  // Ïä§ÌÅ¨Î°§ Î≥µÏõê
  useLayoutEffect(() => {
    const savedPos = sessionStorage.getItem(`scroll_pos:${fullPath}`);
    if (savedPos) {
      const targetY = parseInt(savedPos, 10);
      
      // 1. Ï¶âÏãú Ïù¥Îèô
      window.scrollTo(0, targetY);

      // 2. Î†àÏù¥ÏïÑÏõÉ Î≥ÄÌôî Í∞êÏßÄ Î∞è ÏúÑÏπò Í≥†Ï†ï (1Ï¥àÍ∞Ñ)
      // Ïù¥ÎØ∏ÏßÄÍ∞Ä Îä¶Í≤å Îñ†ÏÑú ÎÜíÏù¥Í∞Ä Î≥ÄÌï¥ÎèÑ Ïä§ÌÅ¨Î°§ ÏúÑÏπòÎ•º targetYÎ°ú Í≥ÑÏÜç Ïû°ÏïÑÎë†
      const observer = new ResizeObserver(() => {
        if (Math.abs(window.scrollY - targetY) > 5) { // 5px Ïù¥ÏÉÅ Ï∞®Ïù¥ÎÇòÎ©¥ Î≥¥Ï†ï
          window.scrollTo(0, targetY);
        }
      });
      observer.observe(document.body);

      const timer = setTimeout(() => {
        observer.disconnect();
      }, 1000); // 1Ï¥à ÌõÑÏóêÎäî ÏÇ¨Ïö©ÏûêÍ∞Ä Ïä§ÌÅ¨Î°§ Ìï† Ïàò ÏûàÍ≤å ÎÜîÏ§å

      return () => {
        observer.disconnect();
        clearTimeout(timer);
      };
    } else {
        // Ï†ÄÏû•Îêú ÏúÑÏπòÍ∞Ä ÏóÜÏúºÎ©¥ Îß® ÏúÑÎ°ú (ÏÉà ÌéòÏù¥ÏßÄ ÏßÑÏûÖ Îì±)
        window.scrollTo(0, 0);
    }
  }, [fullPath]);

  return null;
};

export default ScrollManager;